import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193511 from "../images/WeirdTales_1935_11_ShadowsInZamboula.jpg";

class ConanShadowsInZamboula extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193511} imageDescription="Wierd Tales, November 1935" />

                <h2>Shadows in Zamboula</h2>

                <p>
                    "Shadows in Zamboula" is a fantasy short story by American writer <b>Robert E. Howard</b>. Author's original title is "The Man-Eaters of Zamboula". 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>November 1935</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanShadowsInZamboula;