import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ImageContainerLarge from "../imageContainerLarge.js";
import imageConanSitting from '../images/ConanSitting.jpg';
import imageConanComic from '../images/Conan1970.jpg';
import imageConanMovie from '../images/Conan1982.jpg';
import imageFrazetta from '../images/Frazetta.jpg';

class Conan extends Component {
    render() {
        return (
            <div>
                <h2>Conan the Barbarian</h2>
                <div>
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageConanSitting} imageDescription="Conan the Barbarian, Art by John Buscema" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageConanComic} imageDescription="The Coming of Conan, October 1970 (The first Marvel comic book of Conan)" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageConanMovie} imageDescription="Conan the Barbarian, 1982 (The first movie of Conan)" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageFrazetta} imageDescription="Conan vs Frost Giants, Art by Frank Frazetta" />

                    <p>
                        Conan the Barbarian is a fictional sword and sorcery hero created by the writer <b>Robert E. Howard</b> in 1932 for a series of fantasy stories published in Weird Tales magazine.
                        Since his debut in December of 1932, Conan has been the most famous hero of sword and sorcery.<br/>
                    </p>

                    <h2>Conan's Biograbhy</h2>

                    <p>
                        Conan was born on a battlefield and is the son of a village blacksmith. 
                        Conan matured quickly as a youth and, by age fifteen, he was already a respected warrior who had participated in the destruction of the Aquilonian fortress of Venarium.<br/><br/>
                        
                        After its demise, he was struck by wanderlust and began the adventures chronicled by Howard, encountering skulking monsters, evil wizards, tavern wenches, and beautiful princesses. 
                        He roamed throughout the Hyborian Age nations as a thief, outlaw, mercenary, and pirate.<br/><br/>

                        As he grew older, he began commanding vast units of warriors and escalating his ambitions. 
                        In his forties, he seized the crown from the tyrannical king of Aquilonia, the most powerful kingdom of the Hyborian Age, having strangled the previous ruler on the steps of his own throne. 
                        Conan's adventures often result in him performing heroic feats, though his motivation for doing so is largely to protect his own survival or for personal gain.<br/>
                    </p>

                    <h2>Cimmerians</h2>

                    <p>
                        Conan is a Cimmerian. 
                        The writings of Robert E. Howard (particularly his essay "The Hyborian Age") suggests that his Cimmerians are based on the <b>Celts</b> or perhaps the <b>historic Cimmerians</b>. Howard imagined the Cimmerians as a pre-Celtic people with mostly black hair and blue or grey eyes. <br/><br/>
                        Ethnically, the Cimmerians to which Conan belongs are descendants of the Atlanteans, though they do not remember their ancestry. 
                        In his fictional historical essay "The Hyborian Age", Howard describes how the people of Atlantis, the land where his character King Kull originated, had to move east after a great cataclysm changed the face of the world and sank their island, settling where Ireland and Scotland would eventually be located. 
                        Thus they are (in Howard's work) the ancestors of the Irish and Scottish (the Celtic Gaels) and not the Picts, the other ancestor of modern Scots who also appear in Howard's work. 
                        In the same work, Howard also described how the Cimmerians eventually moved south and east after the age of Conan (presumably in the vicinity of the Black Sea, where the historical Cimmerians dwelt).<br/>
                    </p>

                    <h2>Conan's Characteristics</h2>

                    <p>
                        In his stories Howard described Conan in the following manner.
                        <ul>
                            <li><b>eyes:</b> "sullen", "smoldering", and "volcanic" blue eyes</li>
                            <li><b>hair:</b> black "square-cut mane"</li>
                            <li><b>skin:</b> His skin is frequently characterized as bronzed from constant exposure to the sun.</li>
                            <li><b>height:</b> Howard never gave a strict height or weight for Conan in a story, only describing him in loose terms like "giant" and "massive". In a letter to P. Schuyler Miller and John D. Clark in 1936, only three months before Howard's death, Conan is described as standing 6 ft/183 cm and weighing 180 pounds (82 kg) when he takes part in an attack on Venarium at only 14 years old, though being far from fully grown. Conan himself says in "Beyond the Black River" that he had "...not yet seen 15 snows".</li>
                            <li><b>strength:</b> In the tales, no human is ever described as being stronger than Conan, although several are mentioned as taller (such as the strangler Baal-pteor) or of larger bulk.</li>
                            <li><b>agility:</b> Although Conan is muscular, Howard frequently compares his agility and way of moving to that of a panther (see, for instance, "Jewels of Gwahlur", "Beyond the Black River", or "Rogues in the House"). </li>
                        </ul>
                    </p>

                    <h2>King Conan</h2>

                    <p>
                        During his reign as king of Aquilonia, Conan is described as:
                    </p>                   

                    <p className="tabbedParagraph">
                        "... a tall man, mightily shouldered and deep of chest, with a massive corded neck and heavily muscled limbs. 
                        He was clad in silk and velvet, with the royal lions of Aquilonia worked in gold upon his rich jupon, and the crown of Aquilonia shone 
                        on his square-cut black mane; but the great sword at his side seemed more natural to him than the regal accoutrements. 
                        His brow was low and broad, his eyes a volcanic blue that smoldered as if with some inner fire. His dark, scarred, almost sinister face 
                        was that of a fighting-man, and his velvet garments could not conceal the hard, dangerous lines of his limbs."
                    </p>

                    <h2>Conan through the years</h2>

                    <p>
                        <ul>
                            <li><b>1932-1936:</b> Conan short stories and one novel, written by Robert E. Howard, are being published in Weird Tales pulp magazine.</li>
                            <li><b>1960-1970:</b> Amazing artwork of Conan by Frank Frazetta</li>
                            <li><b>1970-1980:</b> Marvel Comics series of Conan</li>
                            <li><b>1982-1984:</b> Two movies with Arnold Schwarzenegger as Conan</li>
                        </ul>
                    </p>

                    <h2>Novelettes by Robert E. Howard</h2>

                    <p>
                        Here is the list of fully completed novelettes of Conan by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/conan-thephoenixonthesword"><b>"The Phoenix on the Sword" </b><i>(Weird Tales, December 1932)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thescarletcitadel"><b>"The Scarlet Citadel" </b><i>(Weird Tales, January 1933)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thetoweroftheelephant"><b>"The Tower of the Elephant" </b><i>(Weird Tales, March 1933)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-blackcolossus"><b>"Black Colossus" </b><i>(Weird Tales, June 1933)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-theslitheringshadow"><b>"The Slithering Shadow" </b><i>(Weird Tales, September 1933)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thepooloftheblackone"><b>"The Pool of the Black One" </b><i>(Weird Tales, October 1933)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-roguesinthehouse"><b>"Rogues in the House" </b><i>(Weird Tales, January 1934)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-godsofthenorth"><b>"Gods of the North" </b><i>(Fantasy Fan, March 1934)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-ironshadowsinthemoon"><b>"Iron Shadows in the Moon" </b><i>(Weird Tales, April 1934)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-queenoftheblackcoast"><b>"Queen of the Black Coast" </b><i>(Weird Tales, May 1934)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thedeviliniron"><b>"The Devil in Iron" </b><i>(Weird Tales, August 1934)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-awitchshallbeborn"><b>"A Witch Shall Be Born" </b><i>(Weird Tales, December 1934)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-jewelsofgwahlur"><b>"Jewels of Gwahlur" </b><i>(Weird Tales, March 1935)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-beyondtheblackriver"><b>"Beyond the Black" River </b><i>(Weird Tales, May 1935)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-shadowsinzamboula"><b>"Shadows in Zamboula" </b><i>(Weird Tales, November 1935)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-rednails"><b>"Red Nails" </b><i>(Weird Tales, July 1936)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thegodinthebowl"><b>"The God in the Bowl" </b><i>(Space Science Fiction, September 1952)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-theblackstranger"><b>"The Black Stranger" </b><i>(Fantasy Magazine, February 1953)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thevaleoflostwomen"><b>"The Vale of Lost Women" </b><i>(The Magazine of Horror, Spring 1967)</i></NavLink></li>
                        </ul>
                    </p>

                    <h2>Novellas by Robert E. Howard</h2>

                    <p>
                        Here is the list of fully completed novellas of Conan by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/conan-thepeopleoftheblackcircle"><b>"The People of the Black Circle" </b><i>(Weird Tales, September 1934)</i></NavLink></li>
                        </ul>
                    </p>

                    <h2>Novels by Robert E. Howard</h2>

                    <p>
                        Here is the list of fully completed novels of Conan by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/conan-thehourofthedragon"><b>"The Hour of the Dragon" </b><i>(Weird Tales, December 1935)</i></NavLink></li>
                        </ul>
                    </p>


                    <h2>Unfinished stories by Robert E. Howard</h2>

                    <p>
                        Here is the list of unfinished stories of Conan by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/conan-drumsoftombalku"><b>"Drums of Tombalku" </b><i>(Conan the Adventurer, 1966)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thehallofthedead"><b>"The Hall of the Dead" </b><i>(The Magazine of Fantasy and Science Fiction, February 1967)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thehandofnergal"><b>"The Hand of Nergal" </b><i>(Conan, 1967)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-thesnoutinthedark"><b>"The Snout in the Dark" </b><i>(Conan of Cimmeria, 1969)</i></NavLink></li>
                        </ul>
                    </p>

                    <h2>Conan related writings by Robert E. Howard</h2>

                    <p>
                        Here is the list of Conan related writings by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/conan-thehyborianage"><b>"The Hyborian Age" </b><i>(The Hyborian Age, 1938)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-cimmeria"><b>"Cimmeria" </b><i>(The Howard Collector, 1965)</i></NavLink></li>
                            <li><NavLink className="textLink" to="/conan-wolvesbeyondtheborder"><b>"Wolves Beyond the Border" </b><i>(Conan the Usurper, 1967)</i></NavLink></li>
                        </ul>
                    </p>

                    <h2>Comics</h2>

                    <p>
                        The first comic book adaptation of a Howard Conan story was the feature <b>"La reina de la Costa Negra" </b> 
                        (taken from the original Conan story, "Queen of the Black Coast") in Mexico in 1952.<br/><br/>

                        However, the true beginning of Conan's life in comics is considered to be in 1970, when <b>Marvel</b> started comic book series called "Conan the Barbarian".<br/><br/>

                        Beside Marvel, <b>Dark Horse Comics</b> was also publishing Conan comics from 2003 to 2018.
                    </p>

                    <h2>Films</h2>

                    <p>
                        Several Conan movies were produced:
                        <ul>
                            <li><b>Conan the Barbarian (1982)</b>, with Arnold Schwarzenegger as Conan</li>
                            <li><b>Conan the Destroyer (1984)</b>, with Arnold Schwarzenegger as Conan</li>
                            <li><b>Red Sonja (1985)</b>, not about Conan, but Conan related movie with Brigite Nielsen and Arnold Schwarzenegger in main roles</li>
                            <li><b>Conan the Barbarian (2011)</b>, with Jason Momoa as Conan</li>
                        </ul>
                    </p>
                </div>
            </div>
        );
    }
}

export default Conan;