import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193309 from "../images/WeirdTales_1933_09_TheSlitheringShadow.jpg";

class ConanTheSlitheringShadow extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193309} imageDescription="Wierd Tales, September 1933" />

                <h2>The Slithering Shadow</h2>

                <p>
                    "The Slithering Shadow" is a fantasy short story by American writer <b>Robert E. Howard</b>. Alternate title is "Xuthal of the Dusk".
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>September 1933</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheSlitheringShadow;




