import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193512 from "../images/WeirdTales_1935_12_TheHourOfTheDragon.jpg";
import imgWierdTales193601 from "../images/WeirdTales_1936_01_TheHourOfTheDragon.jpg";
import imgWierdTales193602 from "../images/WeirdTales_1936_02_TheHourOfTheDragon.jpg";
import imgWierdTales193603 from "../images/WeirdTales_1936_03_TheHourOfTheDragon.jpg";
import imgWierdTales193604 from "../images/WeirdTales_1936_04_TheHourOfTheDragon.jpg";

class ConanTheHourOfTheDragon extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193512} imageDescription="Wierd Tales, December 1935" />
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193601} imageDescription="Wierd Tales, January 1936" />
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193602} imageDescription="Wierd Tales, February 1936" />
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193603} imageDescription="Wierd Tales, March 1936" />
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193604} imageDescription="Wierd Tales, April 1936" />
               
                <h2>The Hour of the Dragon</h2>

                <p>
                    "The Hour of the Dragon" is the first and the only novel about Conan written by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    Since this novel is much larger then previous short stories, it was being published in parts from <b>December 1935</b> till <b>April 1936</b> in the pulp magazine <b>Weird Tales</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheHourOfTheDragon;