import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ImageContainerLarge from "../imageContainerLarge.js";
import imageCover from "../images/SolomonKane.jpg";
import imageCover2 from "../images/MonstersUnleashed_Solomon_No1.jpg";
import imageCover3 from "../images/SolomonMovie2.jpg";

class SolomonKane extends Component {
    render() {
        return (
            <div>
                <h2>Solomon Kane</h2>
                <p>
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageCover} imageDescription="Solomon Kane, Art by John Buscema" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageCover2} imageDescription="Monsters Unleashed, 1973 (The first comic of Solomon Kane)" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageCover3} imageDescription="Solomon Kane, 2009 (The first movie of Solomon Kane)" />

                    Solomon Kane is a fictional character created by the famous American writer <b>Robert E. Howard</b>. 
                    A late 16th–early 17th century Puritan, Solomon Kane is a somber-looking man who wanders the world with no apparent goal other than to vanquish evil in all its forms. 
                    His adventures, published mostly in the pulp magazine Weird Tales, often take him from Europe to the jungles of Africa and back.
                </p>

                <h2>Appearance</h2>

                <p>
                    Howard described him as a tall, sombre and gloomy man of pale skin, gaunt face and cold eyes, all of it shadowed by a slouch hat. 
                    He is dressed entirely in black and his weaponry usually consists of a rapier, a dirk, and a brace of flintlock pistols. 
                </p>

                <h2>Solomon Kane's Allies</h2>

                <p>
                    The next characters are Solomon's allies:
                    <ul>
                        <li><b>N'Longa</b></li>
                        <li><b>John Silent</b></li>
                    </ul>
                </p>

                <h2>Solomon Kane's Enemies</h2>
                <p>
                    The next characters are Solomon's enemies:
                    <ul>
                        <li><b>Le Loup</b></li>
                        <li><b>The Fishhawk</b></li>
                    </ul>
                </p>

                <h2>Staff of Solomon</h2>

                <p>
                    During one of his later adventures his friend N'Longa, an African shaman, gave him a juju staff that served as a protection against evil but could easily be wielded as a weapon. 
                    It is revealed in another story, "The Footfalls Within", that this is the mythical Staff of Solomon, a talisman older than the Earth and unimaginably powerful, much more so than even N'Longa knew. 
                    In the same adventure with N'Longa, Kane is seen using a musket as well.
                </p>

                <h2>Stories by Robert E. Howard</h2>

                <p>
                    Here is the list of fully completed stories of Solomon Kane by Robert E. Howard:
                    <ul>
                        <li><NavLink className="textLink" to="/solomonkane-redshadows"><b>"Red Shadows" </b> (Weird Tales, August 1928)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-skullsinthestars"><b>"Skulls in the Stars" </b> (Weird Tales, January 1929)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-rattleofbones"><b>"Rattle of Bones" </b> (Weird Tales, Juny 1929)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-themoonofskulls"><b>"The Moon of Skulls" </b> (Weird Tales, Juny 1930)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-hillsofthedead"><b>"Hills of the Dead" </b> (Weird Tales, August 1930)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-thefootfallswithin"><b>"The Footfalls Within" </b> (Weird Tales, September 1931)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-wingsinthenight"><b>"Wings in the Night" </b> (Weird Tales, July 1932)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-bladesofthebrotherhood"><b>"Blades of the Brotherhood" </b> (Red Shadows, 1968)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-therighthandofdoom"><b>"The Right Hand of Doom" </b> (Red Shadows, 1968)</NavLink></li>
                    </ul>
                </p>

                <h2>Unfinished stories by Robert E. Howard</h2>

                <p>
                    Here is the list of unfinished stories of Solomon Kane by Robert E. Howard:
                    <ul>
                        <li><NavLink className="textLink" to="/solomonkane-deathsblackriders"><b>"Death's Black Riders" </b> (The Howard Collector 10, Spring 1968)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-thecastleofthedevil"><b>"The Castle of the Devil" </b> (Red Shadows, 1968)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-thechildrenofasshur"><b>"The Children of Asshur" </b> (Red Shadows, 1968)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-hawkofbasti"><b>"Hawk of Basti" </b> (Red Shadows, 1968)</NavLink></li>
                    </ul>
                </p>

                <h2>Poems by Robert E. Howard</h2>
                
                <p>
                    Here is the list of poems of Solomon Kane by Robert E. Howard:
                    <ul>
                        <li><NavLink className="textLink" to="/solomonkane-solomonkaneshomecoming"><b>"Solomon Kane's Homecoming" </b> (Fanciful Tales, Fall 1936)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-theoneblackstain"><b>"The One Black Stain" </b> (The Howard Collector 2, Spring 1962)</NavLink></li>
                        <li><NavLink className="textLink" to="/solomonkane-thereturnofserrichardgrenville"><b>"The Return of Sir Richard Grenville" </b> (Red Shadows, 1968)</NavLink></li>
                    </ul>
                </p>

                <h2>Comics</h2>
                <p>
                    Series of comics with Solomon Kane as the main character were published by Marvel and Dark Hors. 
                    The first appearance of Solomon Kane in comics was in August 1973 in the Marvel comic magazine <b>"Monsters Unleashed"</b>.
                </p>

                <h2>Films</h2>
                <p>
                    In 2009, a film "Solomon Kane" is produced, with James Purefoy as Solomon Kane.
                </p>
            </div>
        );
    }
}

export default SolomonKane;