import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Bran_1969_00_BranMakMorn_ASongOfTheRace.jpg";

class BranASongOfTheRace extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Bran Mak Morn, 1969" />

                <h2>A Song of the Race</h2>

                <p>
                    "A Song of the Race" is a poem by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>Bran Mak Morn</b> in <b>1969</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Bran Mak Morn</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default BranASongOfTheRace;