import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgageCover from "../images/FantasyMagazine_1953_02_TheBlackStranger.jpg";

class ConanTheBlackStranger extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgageCover} imageDescription="Fantasy Magazine, February 1953" />

                <h2>The Black Stranger</h2>

                <p>
                    "The Black Stranger" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the <b>Fantasy Magazine</b> in <b>February 1953</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheBlackStranger;