import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193409 from "../images/WeirdTales_1934_09_ThePeopleOfTheBlackCircle.jpg";
import imgWierdTales193410 from "../images/WeirdTales_1934_10_ThePeopleOfTheBlackCircle.jpg";
import imgWierdTales193411 from "../images/WeirdTales_1934_11_ThePeopleOfTheBlackCircle.jpg";

class ConanThePeopleOfTheBlackCircle extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193409} imageDescription="Wierd Tales, September 1934" />
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193410} imageDescription="Wierd Tales, October 1934" />
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193411} imageDescription="Wierd Tales, November 1934" />

                <h2>The People of the Black Circle</h2>

                <p>
                    "The People of the Black Circle" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> as a thre-part story from <b>September</b> till <b>October, 1934</b>. 
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanThePeopleOfTheBlackCircle;




