import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/ConanTheUsurper_1967_WolvesBeyondTheBorder.jpg";

class ConanWolvesBeyondTheBorder extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Conan the Usurper, 1967" />

                <h2>Wolves Beyond the Border</h2>

                <p>
                    "Wolves Beyond the Border" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                    It is unfinished a non-Conan story set in Conan's world (Conan-related material).
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>1967</b>.
                </p>
            </div>
        );
    }
}

export default ConanWolvesBeyondTheBorder;