import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/ConanTheAdventurer_1966_00_DrumsOfTombalku.jpg";

class ConanDrumsOfTombalku extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Conan the Adventurer, 1966" />

                <h2>Drums of Tombalku</h2>

                <p>
                    "Drums of Tombalku" is a fantasy, unfinished story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the magazine <b>Conan the Adventurer</b> in <b>1966</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanDrumsOfTombalku;