import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193306 from "../images/WeirdTales_1933_06_BlackColossus.jpg";

class ConanTheBlackColossus extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193306} imageDescription="Wierd Tales, June 1933" />

                <h2>Black Colossus</h2>

                <p>
                    "Black Colossus" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>June 1933</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheBlackColossus;




