import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1968_03_TheHowardCollector10s_DeathsBlackRiders.jpg";

class SolomonDeathsBlackRiders extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="The Howard Collector 10, Spring 1968" />

                <h2>Death's Black Riders</h2>

                <p>
                    "Death's Black Riders" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>The Howard Collector 10</b> in <b>Spring 1968</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonDeathsBlackRiders;