import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1962_03_TheHowardCollector2_TheOneBlackStain.jpg";

class SolomonTheOneBlackStain extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="The Howard Collector 2, Spring 1962" />

                <h2>The One Black Stain</h2>

                <p>
                    "The One Black Stain" is a poem by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>The Howard Collector 2</b> in <b>Spring 1962</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonTheOneBlackStain;