import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgTheKingAndTheOak from "../images/WeirdTales_1939_02_TheKingAndTheOak.jpg";

class KingKullTheKingAndTheOak extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgTheKingAndTheOak} imageDescription="Wierd Tales, February 1939" />

                <h2>The King and the Oak</h2>

                <p>
                    "The King and the Oak" is the only poem written by <b>Robert E. Howard</b>. It is published after his death.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>February 1939</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The poem introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b></li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullTheKingAndTheOak;