import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193212 from "../images/WeirdTales_1932_12_ThePhoenixOnTheSword.jpg";

class ConanThePhoenixOnTheSword extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193212} imageDescription="Wierd Tales, December 1932" />

                <h2>The Phoenix on the Sword</h2>

                <p>
                    "The Phoenix on the Sword" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                    It is the 1<sup>st</sup> of his Conan stories. 
                    The tale, in which Howard created the character of Conan, was a rewrite of the unpublished Kull story, <b>"By This Axe I Rule!"</b>, with long passages being identical.
                </p>

                <p>
                    The first Conan story depicts him as a king—which means that when writing later stories placed earlier in Conan's life, as a thief, pirate, mercenary, etc., Howard (and his readers) already knew that the character was ultimately destined to become a king.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>December 1932</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Conan</b> - the protagonist (The ruller of Aquilonia) </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanThePhoenixOnTheSword;




