import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1930_06_WeirdTales_TheMoonOfSkulls.jpg";
import imageCover2 from "../images/Solomon_1930_07_WeirdTales_TheMoonOfSkulls.jpg";

class SolomonTheMoonOfSkulls extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Wierd Tales, Juny 1930" />
                <ImageContainer className="imageContainer" imageSource={imageCover2} imageDescription="Wierd Tales, July 1930" />

                <h2>The Moon Of Skulls</h2>

                <p>
                    "The Moon Of Skulls" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published as a two part story. 
                    Part 1 is first published in the pulp magazine <b>Weird Tales</b> in <b>Juny 1930</b>.
                    Part 2 is first published in the pulp magazine <b>Weird Tales</b> in <b>July 1930</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonTheMoonOfSkulls;