import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Bran_1932_12_WeirdTales_WormsOftheEarth.jpg";

class BranWormsOftheEarth extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Wierd Tales, December 1932" />

                <h2>Worms of the Earth</h2>

                <p>
                    "Worms of the Earth" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>December 1932</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Bran Mak Morn</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default BranWormsOftheEarth;