import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgTheMirrorsOfTuzunThune from "../images/WeirdTales_1929_09_TheMirrorsOfTuzunThune.jpg";

class KingKullTheMirrorsOfThuzunThune extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgTheMirrorsOfTuzunThune} imageDescription="Wierd Tales, August 1929" />

                <h2>The Mirrors of Thuzun Tune</h2>

                <p>
                    "The Mirrors of Tuzun Thune" is a fantasy short story by American author <b>Robert E. Howard</b>, one of his original short stories about Kull of Atlantis. It is the 2<sup>nd</sup> of only 3 Kull stories to be published in Howard's lifetime.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>September 1929</b>.  
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b> - the protagonist (The ruller of Valusia) </li>
                        <li><b>Brule the Spear-Slayer</b> - a supporting character (Fearsome warrior and Kull's loyal companion)</li>
                        <li><b>Tuzun Thune</b> - antagonist (A wizard of the Elder Race)</li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullTheMirrorsOfThuzunThune;