import React, { Component } from "react";

class CloseButton extends Component {
    constructor(props) {
        super(props);
    }

    closeClicked = () => {
        this.props.close();
    }

    render() {
        return (
            <div className="closeButton" onClick={this.closeClicked}>
               x
            </div>
        );
    }
}

export default CloseButton;