import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193310 from "../images/WeirdTales_1933_10_ThePoolOfTheBlackOne.jpg";

class ConanThePoolOfTheBlackOne extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193310} imageDescription="Wierd Tales, October 1933" />

                <h2>The Pool of the Black One</h2>

                <p>
                    "The Pool of the Black One" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>October 1933</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanThePoolOfTheBlackOne;




