import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/ConanOfCimmeria_1969_00_TheSnoutInTheDark.jpg";

class ConanTheSnoutInTheDark extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Conan of Cimmeria, 1969" />

                <h2>The Snout in the Dark</h2>

                <p>
                    "The Snout in the Dark" is an unfinished fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the magazine <b>Conan of Cimmeria</b> in <b>1969</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheSnoutInTheDark;