import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/MagazineOfHorror_1967_03_TheValeOfLostWomen.jpg";

class ConanTheValeOfLostWomen extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Magazine of Horror, Spring 1967" />

                <h2>The Vale of Lost Women</h2>

                <p>
                    "The Vale of Lost Women" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Magazine of Horror</b> in <b>Spring 1967</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheValeOfLostWomen;