import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193412 from "../images/WeirdTales_1934_12_AWitchShallBeBorn.jpg";

class ConanAWitchShallBeBorn extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193412} imageDescription="Wierd Tales, December 1934" />

                <h2>A Witch Shall Be Born</h2>

                <p>
                    "A Witch Shall Be Born" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>December 1934</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanAWitchShallBeBorn;




