import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1936_00_FancifulTales_SolomonsKaneHomecoming.jpg";

class SolomonSolomonsKaneHomecoming extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Fanciful Tales, Fall 1936" />

                <h2>Solomons Kane's Homecoming</h2>

                <p>
                    "Solomon Kane's Homecoming" is a poem by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>Fanciful Tales</b> in <b>Fall 1936</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonSolomonsKaneHomecoming;