import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/TheMagazineOfFantasyAndScienceFiction_1967_02_TheHallOfTheDead.jpg";

class ConanTheHallOfTheDead extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="The Magazine of Fantasy and Science Fiction, February 1967" />

                <h2>The Hall of the Dead</h2>

                <p>
                    "The Hall of the Dead" is an unfinished synopsis by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>The Magazine of Fantasy and Science Fiction</b> in <b>February 1967</b>.
                </p>
            </div>
        );
    }
}

export default ConanTheHallOfTheDead;