import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgKingKull197800 from "../images/Kull_1978_00_RidersBeyondTheSunrise.jpg";

class KingKullRidersBeyondTheSunrise extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgKingKull197800} imageDescription="Kull, 1978" />

                <h2>Riders Beyond the Sunrise</h2>

                <p>
                    "Riders Beyond the Sunrise" is a fantasy short story by American writer <b>Robert E. Howard</b>.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>Kull</b> in <b>1978</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b> - the protagonist (The ruller of Valusia) </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullRidersBeyondTheSunrise;


