import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Conan_1967_00_TheHandOfNergal.jpg";

class ConanTheHandOfNergal extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Conan, 1967" />

                <h2>The Hand of Nergal</h2>

                <p>
                    "The Hand of Nergal" is an unfinished fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Conan</b> in <b>1967</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheHandOfNergal;