import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193401 from "../images/WeirdTales_1934_01_RoguesInTheHouse.jpg";

class ConanRoguesInTheHouse extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193401} imageDescription="Wierd Tales, January 1934" />

                <h2>Rogues in the House</h2>

                <p>
                    "Rogues in the House" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>January 1934</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanRoguesInTheHouse;




