import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193404 from "../images/WeirdTales_1934_04_IronShadowsInTheMoon.jpg";

class ConanIronShadowsInTheMoon extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193404} imageDescription="Wierd Tales, April 1934" />

                <h2>Iron Shadows in the Moon</h2>

                <p>
                    "Iron Shadows in the Moon" is a fantasy short story by American writer <b>Robert E. Howard</b>. Alternate title is "Shadows in the Moonlight".
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>April 1934</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanIronShadowsInTheMoon;




