import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193303 from "../images/WeirdTales_1933_03_TheTowerOfTheElephant.jpg";

class ConanTheTowerOfTheElephant extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193303} imageDescription="Wierd Tales, March 1933" />

                <h2>The Tower of the Elephant</h2>

                <p>
                    "The Tower of the Elephant" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>March 1933</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheTowerOfTheElephant;




