import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/FantasyFan_1934_03_GodsOfTheNorth.jpg";

class ConanGodsOfTheNorth extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Fantasy Fan, March 1934" />

                <h2>Gods of the North</h2>

                <p>
                    "Gods of the North" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the magazine <b>Fantasy Fan</b> in <b>March 1934</b>.
                    It was also published in the magazine <b>The Comming of Conan</b>, but with a different, maybe even more popular, name: <b>"Frost-Giant's Daughter"</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanGodsOfTheNorth;