import React from "react";

class ImageContainer extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div className="imageContainer">
                <img className="image" src={this.props.imageSource}></img>
                <p className="imageDescription">{this.props.imageDescription}</p>
            </div>
        );
    }
}

export default ImageContainer;