import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1968_00_RedShadows_TheReturnOfSerRichardGrenville.jpg";

class SolomonTheReturnOfSerRichardGrenville extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Red Shadows, 1968" />

                <h2>The Return of Ser Richard Grenville</h2>

                <p>
                    "The Return of Ser Richard Grenville" is a poem by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>Red Shadows</b> in <b>1968</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonTheReturnOfSerRichardGrenville;