import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Bran_1931_04_WeirdTales_ChildrenOfTheNight.jpg";

class BranChildrenOfTheNight extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Wierd Tales, April/May 1931" />

                <h2>Children of the Night</h2>

                <p>
                    "Children of the Night" is a fantasy short story by American writer <b>Robert E. Howard</b>.
                    In this tale, Bran does not appear directly but rather the story elaborates on his cult, which first appears in "The Dark Man" 
                    and which seems to have survived into the 20th Century. 
                    Elements in the narrative correspond with the timeline and events noted in "Worms of the Earth". 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>April/May 1931</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>John O'Donnel</b></li>
                        <li><b>John Conrad</b></li>
                        <li><b>Clemants</b></li>
                        <li><b>Professor Kirowan</b></li>
                        <li><b>Taverel</b></li>
                        <li><b>Ketrick</b></li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default BranChildrenOfTheNight;