import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgKingKull196700 from "../images/TheHowardCollector_1967_00_TheCurseOfTheGoldenSkull.jpg";

class KingKullTheCurseOfTheGoldenSkull extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgKingKull196700} imageDescription="The Howard Collector, Spring 1967" />

                <h2>The Curse of the Golden Skull</h2>

                <p>
                    "The Curse of the Golden Skull" is a fantasy short story by American writer <b>Robert E. Howard</b>.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>King Kull</b> in <b>Spring 1967</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b> - the protagonist (The ruller of Valusia) </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullTheCurseOfTheGoldenSkull;


