import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193408 from "../images/WeirdTales_1934_08_TheDevilInIron.jpg";

class ConanTheDevilInIron extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193408} imageDescription="Wierd Tales, August 1934" />

                <h2>The Devil in Iron</h2>

                <p>
                    "The Devil in Iron" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>August 1934</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheDevilInIron;




