import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193607 from "../images/WeirdTales_1936_07_RedNails.jpg";

class ConanRedNails extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193607} imageDescription="Wierd Tales, July 1936" />

                <h2>Red Nails</h2>

                <p>
                    "Red Nails" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>July 1936</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanRedNails;