import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193503 from "../images/WeirdTales_1935_03_JewelsOfGwahlur.jpg";

class ConanJewelsOfGwalhur extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193503} imageDescription="Wierd Tales, March 1935" />

                <h2>Jewels of Gwahlur</h2>

                <p>
                    "Jewels of Gwahlur" is a fantasy short story by American writer <b>Robert E. Howard</b>.  Author's original title is "The Servants of Bit-Yakin".
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>March 1935</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanJewelsOfGwalhur;




