import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Bran_1931_12_WeirdTales_TheDarkMan.jpg";

class BranTheDarkMan extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Wierd Tales, December 1931" />

                <h2>The Dark Man</h2>

                <p>
                    "The Dark Man" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>December 1931</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Bran Mak Morn</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default BranTheDarkMan;