import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales192908 from "../images/WeirdTales_1929_08_TheShadowKingdom.jpg";

class KingKullTheShadowKingdom extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgWierdTales192908} imageDescription="Wierd Tales, August 1929" />

                <h2>The Shadow Kingdom</h2>

                <p>
                    "The Shadow Kingdom" is a fantasy short story by American writer <b>Robert E. Howard</b>. It is the 1<sup>st</sup> of his Kull stories. The story is set in his fictional Thurian Age. It is considered as one of Howard's most important writings that shaped his carriere.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>August 1929</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                        <ul>
                        <li><b>King Kull</b> - the protagonist (The ruller of Valusia) </li>
                        <li><b>Brule the Spear-Slayer</b> - a supporting character (Fearsome warrior and Kull's loyal companion)</li>
                        <li><b>Serpent Men</b> - antagonists (Anthropomorphic serpents who don't appear in any other work by Howard, but were adopted by later authors for derivative works and inclusion in the Cthulhu Mythos)</li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullTheShadowKingdom;




