import React, { Component } from "react";

class SlidingMenuContent extends Component {
    render() {
        return (
            <div className="slidingMenuContent">
                <h2>About</h2>

                <p className="slidingMenuParagraph">
                    The main purpouse of this site is to present all writings about Conan, Kull, Solomon Kane and Bran Mak Morn, made by Robert E. Howard.<br/><br/>

                    All stories are listed chronologically in each submenu. 
                    For each story the folowing information is presented:<br/>
                    <ul>
                        <li>name</li>
                        <li>first publication date</li>
                        <li>first publication cover</li>
                    </ul>

                    Majority of text about Howard and his heroes is taken from <b>Wikipedia</b> in row form:
                    <ul>
                        <li> <a className="wikiLink" href="https://en.wikipedia.org/wiki/Robert_E._Howard_bibliography">Robert E. Howard</a></li>
                        <li> <a className="wikiLink" href="https://en.wikipedia.org/wiki/Conan_the_Barbarian">Conan</a></li>
                        <li> <a className="wikiLink" href="https://en.wikipedia.org/wiki/Kull_of_Atlantis">King Kull</a></li>
                        <li> <a className="wikiLink" href="https://en.wikipedia.org/wiki/Solomon_Kane">Solomon Kane</a></li>
                        <li> <a className="wikiLink" href="https://en.wikipedia.org/wiki/Bran_Mak_Morn">Bran Mak Morn</a></li>
                    </ul>
                </p>

                <p>

                </p>
            </div>
        );
    }
}

export default SlidingMenuContent;