import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193301 from "../images/WeirdTales_1933_01_TheScarletCitadel.jpg";

class ConanTheScarletCitadel extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193301} imageDescription="Wierd Tales, January 1933" />

                <h2>The Scarlet Citadel</h2>

                <p>
                    "The Scarlet Citadel" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>January 1933</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Conan</b> - the protagonist (The ruller of Aquilonia) </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheScarletCitadel;




