import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgKingsOfTheNight from "../images/WeirdTales_1930_11_KingsOfTheNight.jpg";

class KingKullKingsOfTheNight extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgKingsOfTheNight} imageDescription="Wierd Tales, November 1930" />

                <h2>Kings of the Night</h2>

                <p>
                    "The Shadow Kingdom" is a fantasy short story by American writer <b>Robert E. Howard</b>. It is the 3<sup>rd</sup> and the last of his Kull stories, published during his lifetime.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>November 1930</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                <ul>
                        <li><b>King Kull</b> - the protagonist (The ruller of Valusia) </li>
                        <li><b>Bran Mak Morn</b> - a supporting character (Direct descendant to Brule the Spear-Slayer)</li>
                        <li><b>Romans</b> - antagonists (Enemies to Picts)</li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullKingsOfTheNight;