import React from "react";

class ImageContainerLarge extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div className="imageContainerLarge">
                <img className="imageLarge" src={this.props.imageSource}></img>
                <p className="imageDescriptionLarge">{this.props.imageDescription}</p>
            </div>
        );
    }
}

export default ImageContainerLarge;