import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193405 from "../images/WeirdTales_1934_05_QueenOfTheBlackCoast.jpg";

class ConanQueenOfTheBlackCoast extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193405} imageDescription="Wierd Tales, May 1934" />

                <h2>Queen of the Black Coast</h2>

                <p>
                    "Queen of the Black Coast" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>May 1934</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanQueenOfTheBlackCoast;




