import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1928_08_WeirdTales_RedShadows.jpg";

class SolomonRedShadows extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Wierd Tales, August 1928" />

                <h2>Red Shadows</h2>

                <p>
                    "Red Shadows" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>August 1928</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonRedShadows;