import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/TheHyborianAge_1938_00_TheHyborianAge.jpg";

class ConanTheHyborianAge extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="The Hyborian Age, 1938" />

                <h2>The Hyborian Age</h2>

                <p>
                    "The Hyborian Age" is an essey written by American writer <b>Robert E. Howard</b>. 
                    It is not about Conan, but is Conan-related.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>The Hyborian Age</b> in <b>1938</b>.
                </p>
            </div>
        );
    }
}

export default ConanTheHyborianAge;