import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/SpaceScienceFiction_1952_09_GodInTheBowl.jpg";

class ConanTheGodInTheBowl extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Space Science Fiction, September 1952" />

                <h2>The God in the Bowl</h2>

                <p>
                    "The God in the Bowl" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the magazine <b>Space science Fiction</b> in <b>September 1952</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanTheGodInTheBowl;