import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/Solomon_1929_01_WeirdTales_SkullsInTheStars.jpg";

class SolomonSkullsInTheStars extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="Wierd Tales, january 1929" />

                <h2>Skulls in the Stars</h2>

                <p>
                    "Skulls in the Stars" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>January 1929</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Solomon Kane</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default SolomonSkullsInTheStars;