import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgWierdTales193505 from "../images/WeirdTales_1935_05_BeyondTheBlackRiver.jpg";

class ConanBeyondTheBlackRiver extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imgWierdTales193505} imageDescription="Wierd Tales, May 1935" />

                <h2>Beyond the Black River</h2>

                <p>
                    "Beyond the Black River" is a fantasy short story by American writer <b>Robert E. Howard</b>. 
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in the pulp magazine <b>Weird Tales</b> in <b>May 1935</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>Conan</b> - the protagonist </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default ConanBeyondTheBlackRiver;




