import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgKingKull196709 from "../images/KingKull_1967_09a_TheAltarAndTheScorpion.jpg";

class KingKullTheAltarAndTheScorpion extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgKingKull196709} imageDescription="King Kull, September 1967" />

                <h2>The Altar and the Scorpion</h2>

                <p>
                    "The Altar and the Scorpion" is a fantasy short story by American writer <b>Robert E. Howard</b>.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>King Kull</b> in <b>September 1967</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b></li>
                     </ul>
                </p>
            </div>
        );
    }
}

export default KingKullTheAltarAndTheScorpion;


