import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imageCover from "../images/TheHowardCollector_1965_00_Cimmeria.jpg";

class ConanCimmeria extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imageContainer" imageSource={imageCover} imageDescription="The Howard Collector, 1965" />

                <h2>Cimmeria</h2>

                <p>
                    "Cimmeria" is a poem by American writer <b>Robert E. Howard</b>. It is considered as Conan-related material.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>The Howard Collector</b> in <b>1965</b>.
                </p>
            </div>
        );
    }
}

export default ConanCimmeria;