import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgKingKull196709 from "../images/KingKull_1967_09g_TheStrikingOfTheGong.jpg";

class KingKullTheStrikingOfTheGong extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgKingKull196709} imageDescription="King Kull, September 1967" />

                <h2>The Striking of the Gong</h2>

                <p>
                    "The Striking of the Gong" is a fantasy short story by American writer <b>Robert E. Howard</b>.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>King Kull</b> in <b>September 1967</b>. It is a version edited by Lin Carter.
                    Original Howard's version was first published in the "Second Book of Robert E. Howard", 1976.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b> - the protagonist</li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullTheStrikingOfTheGong;


