import React, { Component } from "react";
import ImageContainer from "../imageContainer.js";
import imgKingKull196709 from "../images/KingKull_1967_09c_ByThisAxeIRule.jpg";

class KingKullByThisAxeIRule extends Component {
    render() {
        return (
            <div>
                <ImageContainer className="imgageContainer" imageSource={imgKingKull196709} imageDescription="King Kull, September 1967" />

                <h2>By This Axe I Rule</h2>

                <p>
                    "By This Axe I Rule" is a fantasy short story by American writer <b>Robert E. Howard</b>. It is the last of his Kull stories. 
                    This story was rejected by the pulp magazines "Argosy and Adventure" in 1929, after which Howard rewrote it as the Conan story "The Phoenix on the Sword", substituting a new secondary plot and adding elements of supernatural horror. 
                    The main shared elements of the two stories are the conspiracy and the king's defeat of it. The Conan story was published in December 1932.
                </p>

                <h2>The first publication</h2>

                <p>
                    It was first published in <b>King Kull</b> in <b>September 1967</b>.
                </p>

                <h2>The main characters</h2>

                <p>
                    The story introduces the next main characters:
                    <ul>
                        <li><b>King Kull</b> - the protagonist (The ruller of Valusia) </li>
                    </ul>
                </p>
            </div>
        );
    }
}

export default KingKullByThisAxeIRule;


