import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ImageContainerLarge from "../imageContainerLarge.js";
import imageKullRiding from '../images/KullRiding.jpg';
import imageKull1971 from '../images/Kull1971.jpg';
import imageKull1997 from '../images/Kull1997.jpg';
import imageConanKull from '../images/ConanKull.jpg';

class KingKull extends Component {
    render() {
        return (
            <div>
                <h2>King Kull</h2>
                
                <div>
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageKullRiding} imageDescription="King Kull, Art by John Buscema" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageKull1971} imageDescription="Kull the Conqueror, June 1971 (The first Marvel comic book of King Kull)" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageKull1997} imageDescription="Kull the Conqueror, 1997 (The first movie of King Kull)" />
                    <ImageContainerLarge className="imageContainerLarge" imageSource={imageConanKull} imageDescription="Conan the Barbarian - No 1 - page 15, October 1970 (The first appearance of King Kull)" />

                    <p>
                        Kull of Atlantis or Kull the Conqueror is a fictional character created by writer <b>Robert E. Howard</b>.
                        The character was more introspective than Howard's subsequent creation, Conan the Barbarian, whose first appearance was in a re-write of a rejected Kull story.
                        His first published appearance was <b>"The Shadow Kingdom"</b> in Weird Tales (August, 1929).
                    </p>

                    <h2>Life in Atlantis</h2>

                    <p>
                        Kull was born in pre-cataclysmic Atlantis c. 18,500 BC, depicted as inhabited at the time by barbarian tribes. 
                        He was born into a tribe settled in the <b>Tiger Valley of Atlantis</b>. 
                        Both the valley and tribe were destroyed by a flood while Kull was still a toddler, leaving the young Kull to live as a feral child for many years. 
                        Kull was captured by the Sea-Mountain tribe and eventually adopted by them. 
                        In <b>"Exile of Atlantis"</b>, an adolescent Kull grants a woman a quick death so that she will not be burned to death by a mob. 
                        For this selfless act, Kull is exiled from Atlantis.
                    </p>

                    <h2>Slave, pirate, outlaw, and gladiator</h2>

                    <p>
                        Kull attempted to reach Thuria, but was instead captured by Lemurian Pirates. 
                        He spent a couple of years as a galley slave before regaining his freedom during a mutiny.
                    </p>

                    <p>
                        He tried the life of a pirate between his late adolescence and his early twenties. 
                        His fighting skills and courage allowed him to become captain of his own ship. 
                        Soon, Kull gained a fearsome reputation for himself in the seas surrounding Atlantis and Thuria. 
                        Kull lost his ship and crew in a naval battle off the coast of Valusia, but once again survived. 
                        He settled in Valusia as an outlaw. 
                        However, his criminal career proved to be short-lived as he was soon captured by the Valusians and imprisoned in a dungeon. 
                        His captors offered him a choice: execution or service as a gladiator. 
                        He chose the latter. 
                        After proving to be an effective combatant and gaining fame in the arenas of the capital, a number of fans helped to regain his freedom.
                    </p>

                    <h2>Soldier</h2>

                    <p>
                        Kull never left Valusia or returned to the life of an outlaw. 
                        Instead, he joined the Royal army as a mercenary, pursuing elevation through the ranks. 
                        In <b>"The Curse of the Golden Skull"</b> Kull, approaching his thirties, is recruited by <b>King Borna of Valusia</b> in a mission against the ambitious sorcerer Rotath of Lemuria. 
                        Kull proves to be an effective assassin.
                    </p>

                    <p>
                        Borna promoted Kull into the general command of his mercenary forces. Borna himself, however, had gained a reputation for his cruelty and despotism. 
                        There was discontent with Borna's rule among the nobility, leading eventually to a civil war. 
                        The mercenaries proved more loyal to Kull than any other leader, allowing him to become first the leadership in their revolt and then King. 
                        Kull killed Borna and took the throne while he was still in his early thirties. 
                    </p>

                    <h2>King</h2>

                    <p>
                        In <b>"The Shadow Kingdom"</b>, Kull has spent six months upon the Valusian throne and faces the first conspiracy against him.
                        The series continued with Kull finding that gaining the crown was easier than securing it. 
                        He faces several internal and external challenges throughout the series. 
                        The conspiring of his courtiers leaves Kull almost constantly threatened with loss of life and throne. 
                        The aging King is ever more aware of the Sword of Damocles that he inherited along with the crown.
                    </p>

                    <p>
                        <b>"The Mirrors of Tuzun Thune"</b> finds Kull reaching his middle forties and becoming progressively more introspective. 
                        The former barbarian is left lost in contemplations of philosophy. 
                        At this point, the series ends. 
                        His fate is left uncertain.
                    </p>

                    <h2>Kull's allies</h2>

                    <p>
                        Following is the list of Kull's allies.
                        <ul>
                            <li><b>Brule the Spear-slayer</b> - a pre-cataclysmic Pict, Kull's trusted ally.</li>
                            <li><b>First Councillor Tu</b> - Kull's a trusted administrator, but also a constant reminder of the tradition bound laws and customs of Valusia. </li>
                            <li><b>Ka-Nu</b> - (sometimes named Kananu), the Pictish Ambassador to Valusia and wise man, is responsible for the friendship between Kull and Brule despite the ancient enmity between Atlanteans and Picts.</li>
                        </ul>
                    </p>

                    <h2>Kull's enemies</h2>

                    <p>
                        Following is the list of Kull's enemies.
                        <ul>
                            <li><b>Thulsa Doom</b> - Kull's mortal enemy, the sorcerer.</li>
                        </ul>
                    </p>

                    <h2>Stories by Robert E. Howard</h2>

                    <p>
                        Here is the list of fully completed stories of Kull by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/kingkull-theshadowkingdom"><b>"The Shadow Kingdom" </b> (Weird Tales, August 1929)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-themirrorsoftuzunthune"><b>"The Mirrors of Thuzun Tune" </b> (Weird Tales, September 1929)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-kingsofthenight"><b>"Kings of the Night" </b> (Weird Tales, November 1930)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-thecurseofthegoldenskull"><b>"The Curse of the Golden Skull" </b>(The Howard Collector, Spring 1967)</NavLink></li> 
                            <li><NavLink className="textLink" to="/kingkull-thealtarandthescorpion"><b>"The Altar and the Scorpion" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-theblackabyss"><b>"The Black Abyss" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-bythisaxeirule"><b>"By This Axe I Rule" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-declardescat"><b>"Delcarde's Cat" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-exileofatlantis"><b>"Exile of Atlantis" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-theskullofsilence"><b>"The Skull of Silence" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-thestrikingofthegong"><b>"The Striking of the Gong" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-swordsofthepurplekingdom"><b>"Swords of the Purple Kingdom" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-wizardandwarrior"><b>"Wizard and Warrior" </b>(King Kull, September 1967)</NavLink></li>
                            <li><NavLink className="textLink" to="/kingkull-ridersbeyondthesunrise"><b>"Riders Beyond the Sunrise" </b>(King Kull, 1978)</NavLink></li>                        
                        </ul>
                    </p>

                    <h2>Poems by Robert E. Howard</h2>

                    <p>
                        Here is the list of poems of Kull by Robert E. Howard:
                        <ul>
                            <li><NavLink className="textLink" to="/kingkull-thekingandtheoak"><b>"The King and the Oak" </b> (Weird Tales, February 1939)</NavLink></li>
                        </ul>
                    </p>

                    <h2>Comics</h2>

                    <p>
                        Series of comics with King Kull as the main character were published by Marvel, Dark Horse etc.
                        The first King Kull comic is published by Marvel in June 1971. It was called "Kull the Conqueror".
                        One of the most famous drawers who worked on Kull comics is <b>John Buscema</b>, who is also known for his artistic works on Conan the Barbarian.
                    </p>

                    <h2>Films</h2>

                    <p>Kull was portrayed in the 1997 film <b>Kull the Conqueror</b> by actor Kevin Sorbo.</p>
                </div> 
            </div>
        );
    }
}

export default KingKull;